<template>
  <div class="option page container-full flex flex-column">
    <div class="layout">
      <div class="wrap-inner flex flex-column"
           :class="{'view-chart':viewType}"
      >

        <div class="box up flex jc-between ai-center" ref="touchEl"
             :class="'active-'+roundIndex" :style="touchStyle"
        >
          <div class="round-con">
            <round-box :epoch-offset="2" style="height: 100%;"/>
          </div>


          <div class="lock-round round-con">
            <round-box class="shadow-active" :epoch-offset="1" style="height: 100%;"/>
          </div>

          <div class="bet-con" style="height: 100%;">
            <betting-box/>
          </div>

        </div>


        <div class="box down flex1">
          <div class="flex jc-center">
            <p class="chart-notice flex jc-center ai-center">
              <img class="icon-s" src="../images/option/kline1.png" alt="">
              {{ language.tip_chart }}
            </p>
          </div>
          <chart/>
        </div>
        
      </div>
    </div>
    <div class="mini-bar flex jc-between ai-center">
      <img class="icon btn-left" src="../images/option/jiantou.png"
           :class="{hide:viewType===1||roundIndex===0}"
           @click="prev()" alt="">
      <div class="btn-group flex jc-around ai-center">
        <img v-if="viewType===0" class="icon-s" src="../images/option/option1.png" alt="">
        <img v-else class="icon-s" src="../images/option/option.png" @click="viewType=0" alt="">
        <img v-if="viewType===1" class="icon-s" src="../images/option/kline1.png" alt="">
        <img v-else class="icon-s" src="../images/option/kline.png" @click="viewType=1" alt="">
      </div>
      <img class="icon" src="../images/option/jiantou.png" @click="next()"
           :class="{hide:viewType===1||roundIndex===2}"
           alt="">
    </div>
  </div>
</template>

<script>
import RoundBox from "@/views/optionComps/roundBox";
import BettingBox from "@/views/optionComps/bettingBox";
import Chart from "@/views/optionComps/chart";
import {mapGetters} from "vuex";

export default {
  name: 'options',
  components: {Chart, BettingBox, RoundBox},
  data() {
    return {
      viewType: 0,
      roundIndex: 2,
      touchStyle: {},
      hammer:null,
    };
  },
  computed:{
    ...mapGetters(['language']),
  },
  methods: {
    prev() {
      this.roundIndex > 0 && (this.roundIndex--);
    },
    next() {
      this.roundIndex < 2 && (this.roundIndex++);
    },
    setIndex(idx){
      this.roundIndex = idx;
    },
    bindEvent() {
      let el = this.$refs.touchEl;
      this.hammer = new window.Hammer(el);
      this.hammer.on('swipeleft swiperight', (e) => {
        let {outerWidth} = window;
        if (outerWidth>1024) return;
        switch (e.type) {
          case 'swipeleft':
            outerWidth>540?this.setIndex(2):this.next();
            break;
          case 'swiperight':
            outerWidth>540?this.setIndex(0):this.prev();
            break;
        }
      });
    },
    offEvent(){
      this.hammer?.off('swipeleft swiperight');
    }
  },
  mounted() {
    this.bindEvent();
  },
  unmounted() {
    this.offEvent();
  }
};
</script>

<style lang="less" scoped>
.page{
  padding: 0;
}
.layout {
  padding: 0 60px;
  height: 100%;
  overflow: auto;
  .wrap-inner {
    height: 100%;
  }
}
.up {
  height: 426px;
  padding: 15px 0;
}
.round-con {
  width: 25%;
  height: 100%;
  min-width: 280px;
}
.bet-con {
  width: 50%;
}
.lock-round{
  margin: 0 20px;
}
.down {
  padding-bottom: 15px;
  border-radius: 20px;
  min-height: 370px;
  overflow: hidden;
}
.chart-notice{
  height: 40px;
  margin-bottom: 10px;
  padding: 0 15px 0 10px;
  border-radius: 20px;
  background-color: #191f2d;
  img{
    margin-right: 5px;
  }
}
.chart{
  height: calc(100% - 50px);
  border-radius: 20px;
  overflow: hidden;
}
.h100p {
  height: 100%;
}
.mini-bar {
  padding: 0 15px;
  display: none;
  .hide {
    opacity: 0;
  }
}
.btn-group {
  width: 50%;
  height: 40px;
  border-radius: 20px;
  background-color: #191f2d;
}
.btn-left {
  transform: rotate(-180deg);
}
@media screen and (max-width: 1024px) {
  .page{
    padding: 65px 0 0 0;
  }
  .layout {
    padding: 0;
  }
  .up {
    width: 150vw;
    justify-content: flex-start;
    transition: margin-left ease .2s;
    &.active-0 {
      margin-left: 0;
    }
    &.active-1 {
      margin-left: -50vw;
    }
    &.active-2 {
      margin-left: -50vw;
    }
    .round-con {
      width: 100vw;
      margin-right: 0;
      padding: 0 20px;
    }
    .lock-round{
      margin: 0;
    }
    .bet-con {
      width: 100vw;
      padding: 0 20px;
    }
  }
  .down {
    height: 50%;
    padding: 0 20px 20px;
  }
  .h100p {
    height: auto;
  }
}
@media screen and (max-width: 768px) {
  .up {
    padding: 15px 0;
    .round-con {
      padding: 0 15px;
    }
    .bet-con {
      padding: 0 15px;
    }
  }
  .down {
    padding: 0 15px 15px;
  }
}
@media screen and (max-width: 540px) {
  .page{
    padding-top: 105px;
  }
  .layout {
    height: calc(100% - 50px);
    padding: 0;
    overflow: hidden;
    .wrap-inner {
      display: block;
      position: relative;
      top: 0;
      transition: top ease .2s;
    }
    .view-chart {
      top: -100%;
    }
  }
  .up {
    width: 300vw;
    height: 100%;
    padding: 10px 0;
    margin-bottom: 0;
    &.active-1 {
      margin-left: -100vw;
    }
    &.active-2 {
      margin-left: -200vw;
    }
    .round-con {
      width: 100vw;
      margin-right: 0;
      padding: 0 10px;
    }
    .bet-con {
      width: 100vw;
      padding: 0 10px;
    }
  }
  .down {
    flex: none;
    height: 100%;
    padding: 10px;
  }
  .mini-bar {
    display: flex;
  }
}
</style>